@media (min-width: 1200px) {
    .container {
        max-width: 1200px;
    }
}
body,
div#page-inner {
    /* Will not work on IE if body is ommitted */
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    min-height: 100vh;
}

div#content {
    -ms-flex-positive: 1;
        flex-grow: 1;
}

.posts-title,
.navbar {
    padding-left: 1rem;
}

.navbar {
    border-bottom: 1px solid #eaeaea;
}

.site-content-contain {
    background-color: transparent !important;
    margin-top: 60px;
}

.site-content-contain #content,
.navigation-top {
    background-color: #fff;
    width: 75%;
    margin: 0 auto;
}

@media screen and (min-width: 48em) {
    .site-content {
        padding: 2.5em 0 0;
    }
}
footer#colophon {
    background-color: #fff;
}

.aseel-front-page.has-header-image .custom-header-media,
.aseel-front-page.has-header-video .custom-header-media,
.home.blog.has-header-image .custom-header-media,
.home.blog.has-header-video .custom-header-media {
    height: 25vh !important;
}

#primary #main .post {
    clear: left;
    float: left;
    width: 100%;
}

.post-excerpt {
    background: #f4f4f4;
    padding: 10px;
    margin-bottom: 20px;
}

img#loader {
    clear: both;
    margin: 0 auto;
}

#loader {
    text-align: center;
    display: none;
}

#loader.active {
    display: block;
}

.loader-gif {
    text-align: center;
    margin-top: 10em;
}

.post-entry {
    padding-top: 1em;
}

.col-md-4.card-outer {
    float: left;
    padding-top: 1em;
    padding-bottom: 1em;
}

.col-md-4.card-outer .card {
    height: 520px;
}

.img-outer {
    max-height: 250px;
    overflow: hidden;
}

#posts-here {
    position: fixed;
    bottom: 10px;
}

.card-outer {
    opacity: 0;
    -webkit-transform: translateY(100px);
        -ms-transform: translateY(100px);
            transform: translateY(100px);
    -webkit-transition: all 0.5s ease-out;
    -o-transition: all 0.5s ease-out;
    transition: all 0.5s ease-out;
}

.card-outer.fade-in {
    opacity: 1;
    -webkit-transform: translateY(0);
        -ms-transform: translateY(0);
            transform: translateY(0);
}

.card-body div {
    float: left;
}

img.product-image,
img.featured-image {
    width: 100%;
}



